@tailwind base;
@tailwind components;
@tailwind utilities;

.bg-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-image: url('./images/background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}

.bg-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)); /* Adjust opacity here */
  mix-blend-mode: multiply; /* Darken the background image */
  z-index: 0; /* Ensure the overlay is below the content */
}

.bg-container > * {
  position: relative; /* Ensure text remains above the overlay */
  z-index: 1; /* Ensure the content is above the overlay */
}

.glow-purple {
  box-shadow: 0 0 20px rgba(138, 43, 226, 0.3); /* Adjust the shadow color and intensity here */
}
